"use client"

import React, { useState, useRef } from 'react'

interface CarouselItem {
  id: number
  title: string
  description: string
  imageUrl: string
  category: string
}

const StreamingCarousel = () => {
  const scrollRef = useRef<HTMLDivElement>(null)
  const [currentIndex, setCurrentIndex] = useState(0)

  const featuredContent: CarouselItem[] = [
    {
      id: 1,
      title: "Serie √âpica de Fantas√≠a",
      description: "Una aventura √©pica llena de magia, dragones y h√©roes legendarios que cambiar√°n el destino del mundo.",
      imageUrl: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/d2f2be5f-09dd-421a-b33d-33376a66215b.png",
      category: "Serie Original"
    },
    {
      id: 2,
      title: "Thriller Psicol√≥gico",
      description: "Un misterio que te mantendr√° al borde del asiento con giros inesperados y suspenso constante.",
      imageUrl: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/90302ccf-fd4b-415d-a4fe-24aed2dc1f51.png",
      category: "Pel√≠cula"
    },
    {
      id: 3,
      title: "Comedia Rom√°ntica",
      description: "Una historia de amor moderna llena de humor, romance y momentos que tocar√°n tu coraz√≥n.",
      imageUrl: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/10a2b396-99f1-47ef-b039-7fd4caf040fc.png",
      category: "Pel√≠cula"
    },
    {
      id: 4,
      title: "Documental de Naturaleza",
      description: "Explora los paisajes m√°s impresionantes del planeta y descubre la vida salvaje en su m√°ximo esplendor.",
      imageUrl: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/03016c86-0604-4c3b-a02a-1f9f4ef87a24.png",
      category: "Documental"
    },
    {
      id: 5,
      title: "Acci√≥n y Aventura",
      description: "Adrenalina pura con secuencias de acci√≥n espectaculares y efectos visuales de √∫ltima generaci√≥n.",
      imageUrl: "https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/b5a5d696-9e76-472a-b9f7-e7db4b6cb7bd.png",
      category: "Pel√≠cula"
    }
  ]

  const scrollToIndex = (index: number) => {
    if (scrollRef.current) {
      const scrollAmount = index * scrollRef.current.offsetWidth
      scrollRef.current.scrollTo({
        left: scrollAmount,
        behavior: 'smooth'
      })
      setCurrentIndex(index)
    }
  }

  const handleScroll = () => {
    if (scrollRef.current) {
      const scrollLeft = scrollRef.current.scrollLeft
      const itemWidth = scrollRef.current.offsetWidth
      const newIndex = Math.round(scrollLeft / itemWidth)
      setCurrentIndex(newIndex)
    }
  }

  const handleKeyDown = (event: React.KeyboardEvent, index: number) => {
    if (event.key === 'Enter' || event.key === ' ') {
      event.preventDefault()
      scrollToIndex(index)
    }
  }

  return (
    <section className="relative bg-gradient-to-b from-black to-gray-900 py-8" aria-label="Contenido destacado">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <h2 className="text-3xl font-bold text-white mb-6">Destacados</h2>
        
        {/* Carousel Container */}
        <div className="relative">
          <div
            ref={scrollRef}
            className="flex overflow-x-auto scrollbar-hide snap-x snap-mandatory"
            onScroll={handleScroll}
            style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}
            role="region"
            aria-label="Carrusel de contenido destacado"
          >
            {featuredContent.map((item, index) => (
              <div
                key={item.id}
                className="flex-none w-full snap-start relative group cursor-pointer"
                onClick={() => scrollToIndex(index)}
                onKeyDown={(e) => handleKeyDown(e, index)}
                tabIndex={0}
                role="button"
                aria-label={`Ver ${item.title}`}
              >
                <div className="relative h-96 md:h-[500px] rounded-lg overflow-hidden">
                  <img
                    src={item.imageUrl}
                    alt={`${item.title} - ${item.description}`}
                    className="w-full h-full object-cover transition-transform duration-300 group-hover:scale-105"
                    onError={(e) => {
                      e.currentTarget.src = 'https://storage.googleapis.com/workspace-0f70711f-8b4e-4d94-86f1-2a93ccde5887/image/952e29b4-e3db-4738-af67-97cbb6b8d902.png'
                    }}
                  />
                  
                  {/* Gradient Overlay */}
                  <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent"></div>
                  
                  {/* Content Overlay */}
                  <div className="absolute bottom-0 left-0 right-0 p-6 md:p-8">
                    <div className="max-w-2xl">
                      <span className="inline-block px-3 py-1 bg-red-600 text-white text-sm font-medium rounded-full mb-3">
                        {item.category}
                      </span>
                      <h3 className="text-2xl md:text-4xl font-bold text-white mb-3">
                        {item.title}
                      </h3>
                      <p className="text-gray-200 text-sm md:text-base mb-6 line-clamp-3">
                        {item.description}
                      </p>
                      
                      {/* Action Buttons */}
                      <div className="flex flex-wrap gap-3">
                        <button
                          className="px-6 py-3 bg-white text-black font-semibold rounded-md hover:bg-gray-200 transition-colors focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-black"
                          aria-label={`Reproducir ${item.title}`}
                        >
                          ‚ñ∂ Reproducir
                        </button>
                        <button
                          className="px-6 py-3 bg-gray-600/80 text-white font-semibold rounded-md hover:bg-gray-500/80 transition-colors focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-black"
                          aria-label={`M√°s informaci√≥n sobre ${item.title}`}
                        >
                          ‚Ñπ M√°s informaci√≥n
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Navigation Dots */}
          <div className="flex justify-center mt-6 space-x-2" role="tablist" aria-label="Navegaci√≥n del carrusel">
            {featuredContent.map((_, index) => (
              <button
                key={index}
                className={`w-3 h-3 rounded-full transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-black ${
                  currentIndex === index
                    ? 'bg-white scale-110'
                    : 'bg-gray-500 hover:bg-gray-300'
                }`}
                onClick={() => scrollToIndex(index)}
                aria-label={`Ir al elemento ${index + 1} del carrusel`}
                role="tab"
                aria-selected={currentIndex === index}
              />
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}

export default StreamingCarousel
